import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
//import { HttpClientModule } from '@angular/common/http';
import { Network } from '@ionic-native/network';
import { HttpModule } from '@angular/http';

// plugins
import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { ServiceProvider } from '../providers/service/service';
import { CommonProvider } from '../providers/common/common';
// import { FCM } from '@ionic-native/fcm';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { QRScanner, QRScannerStatus } from '@ionic-native/qr-scanner';

//page
import { EmpdashboardPageModule } from '../pages/employee/empdashboard/empdashboard.module';
import { NotificationPageModule } from '../pages/notification/notification.module';
import { NotificationDetailPageModule } from '../pages/notification-detail/notification-detail.module';
import { ScanPageModule } from '../pages/scan/scan.module';
//hod Dashboard
import { HoddashboardPageModule } from '../pages/hod/hoddashboard/hoddashboard.module';
import { RequesthistoryPageModule } from '../pages/hod/requesthistory/requesthistory.module';
//Driver page
import { DriverPageModule } from '../pages/driver/driver.module';
import { UsersDashboardPageModule } from '../pages/users-dashboard/users-dashboard.module';
//Admin page
import { AdminrequestsPageModule } from '../pages/adminrequests/adminrequests.module';
import { AdminAprvlPageModule } from '../pages/admin-aprvl/admin-aprvl.module';

import { DatePickerModule } from 'ionic3-datepicker';

@NgModule({
  declarations: [
    MyApp,
    LoginPage
  ],
  imports: [
    BrowserModule,
    //  HttpClientModule,
    EmpdashboardPageModule,
    NotificationPageModule,
    NotificationDetailPageModule,
    HoddashboardPageModule,
    RequesthistoryPageModule,
    UsersDashboardPageModule,
    AdminrequestsPageModule,
    AdminAprvlPageModule,
    HttpModule,
    DriverPageModule,
    ScanPageModule,
    DatePickerModule,

    IonicModule.forRoot(MyApp, {
      backButtonText: 'Back',
      backButtonIcon: ''
    }, 
{
  links: [
    { loadChildren: '../pages/admin-aprvl/admin-aprvl.module.ngfactory#AdminAprvlPageModuleNgFactory', name: 'AdminAprvlPage', segment: 'admin-aprvl', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/driver/driver.module.ngfactory#DriverPageModuleNgFactory', name: 'DriverPage', segment: 'driver', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hod/requesthistory/requesthistory.module.ngfactory#RequesthistoryPageModuleNgFactory', name: 'RequesthistoryPage', segment: 'requesthistory', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/modal-detail/modal-detail.module.ngfactory#ModalDetailPageModuleNgFactory', name: 'ModalDetailPage', segment: 'modal-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification-detail/notification-detail.module.ngfactory#NotificationDetailPageModuleNgFactory', name: 'NotificationDetailPage', segment: 'notification-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification/notification.module.ngfactory#NotificationPageModuleNgFactory', name: 'NotificationPage', segment: 'notification', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/requestdetails/requestdetails.module.ngfactory#RequestdetailsPageModuleNgFactory', name: 'RequestdetailsPage', segment: 'requestdetails', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/scan/scan.module.ngfactory#ScanPageModuleNgFactory', name: 'ScanPage', segment: 'scan', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/users-dashboard/users-dashboard.module.ngfactory#UsersDashboardPageModuleNgFactory', name: 'UsersDashboardPage', segment: 'users-dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/adminrequests/adminrequests.module.ngfactory#AdminrequestsPageModuleNgFactory', name: 'AdminrequestsPage', segment: 'adminrequests', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/employee/empdashboard/empdashboard.module.ngfactory#EmpdashboardPageModuleNgFactory', name: 'EmpdashboardPage', segment: 'empdashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hod/hoddashboard/hoddashboard.module.ngfactory#HoddashboardPageModuleNgFactory', name: 'HoddashboardPage', segment: 'hoddashboard', priority: 'low', defaultHistory: [] }
  ]
})


  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ServiceProvider,
    CommonProvider,
    //  FCM,
    Network,
    InAppBrowser,
    QRScanner

  ]
})
export class AppModule { }



